import '../Fabricante/FabricanteDocumentos.css'
import '@uppy/core/dist/style.css'
import '@uppy/dashboard/dist/style.css'
import '@uppy/image-editor/dist/style.css'

import { Capacitor } from '@capacitor/core'
import Uppy from '@uppy/core'
import { DashboardModal } from '@uppy/react'
import Webcam from '@uppy/webcam'
import { File } from '@ionic-native/file'
import { IonSpinner } from '@ionic/react'
import { observer } from 'mobx-react-lite'
import React, { useEffect, useState } from 'react'
import * as FIcon from 'react-feather'
import { AiOutlineEye } from 'react-icons/ai'
import { useHistory } from 'react-router'

import { Page } from '../../components'
import { ModalInfo } from '../../components/ModalInfo'
import { useApiToaster, useToaster } from '../../helpers-ts'
import { useStores } from '../../models/root-store/root-store-context'
import { Fabricante as FabricanteApi } from '../../services/api'
import { SubHeader } from '../../components/SubHeader'
import { RUTAS_APP_FABRICANTE } from '../../constants'

const FileDownload = require('js-file-download')
const moment = require('moment-timezone')
const ImageCompressor = require('uppy-plugin-image-compressor')
const Espanol = require('@uppy/locales/lib/es_ES')
const XHRUpload = require('@uppy/xhr-upload')

const defaultValues = {
  id: 0,
  nombre: '',
  apellido: '',
  nro_documento: '',
  email: '',
  nro_telefono: '',
  razon_social: '',
  ruc: '',
  registro_frente: null,
  registro_dorso: null,
  registro_vencimiento: '',
  antecedentesj: null,
  antecedentesp: null,
  constancia_ruc: null,
  aprobado: false,
  ciudades: [],
  imagen_perfil: '',
}

export const FabricanteDocumentos: React.FC = observer(() => {
  const rootStore = useStores()
  const { loginStore } = rootStore
  const addToast = useToaster()
  const [Loading, setLoading] = useState(false)
  const [UppyVisible, setUppyVisible] = useState(false)
  const apiToaster = useApiToaster()
  const [TModalInfo, setTModalInfo] = useState<"ruc"|"contrato"|null>(null)
  const [Datos, setDatos] = useState<any>({})
  const [Fabricante, setFabricante] = useState<FabricanteApi | null>()
  const history = useHistory()
  const Movil = window.innerWidth <= 500 || /Mobi/.test(navigator.userAgent)

  React.useEffect(() => {
    if (!loginStore.fabricante) {
      addToast('Error al obtener datos del fabricante', 'error')
      history.goBack()
      return
    }
    obtenerFabricante(loginStore.fabricante.id)
  }, [])

  const obtenerFabricante = (idFabricante: number) => {
    rootStore.environment.api.adminGetFabricante(idFabricante).then((result) => {
      if (result.kind === 'ok') {
        loginStore.setFabricante(result.fabricante)
        setFabricante(result.fabricante)
      }
    })
  }

  const obtenerContrato = () => {
    rootStore.environment.api.getContrato('fabricante').then((result) => {
      if (result.kind === 'ok') {
        if (Capacitor.getPlatform() === 'web') {
          FileDownload(result.data, 'acuerdo-fabricante.pdf')
        } else {
          const dwpath =
            Capacitor.getPlatform() === 'ios' ? `${File.documentsDirectory}` : `${File.externalRootDirectory}Download/`
          const dwpathReadable =
            Capacitor.getPlatform() === 'ios' ? `Documents/acuerdo-fabricante.pdf` : `Download/acuerdo-fabricante.pdf`
          const blobi = new Blob([result.data], { type: 'application/pdf' })
          File.writeFile(dwpath, `acuerdo-fabricante.pdf`, blobi, { replace: true })
            .then((res) => {
              addToast(`Acuerdo descargado exitosamente, guardado en ${dwpathReadable}`)
            })
            .catch((error) => {
              console.log(error)
              addToast(`Error descargando el acuerdo, favor intentelo nuevamente!`, 'error')
            })
        }
      } else {
        apiToaster(result, '', 'descargar contrato')
      }
    })
  }

  const aceptarContrato = () => {
    rootStore.environment.api.aceptarContrato('fabricante').then((result) => {
      if (!loginStore.fabricante) {
        return
      }
      if (result.kind === 'ok') {
        loginStore.setFabricante(result.response)
        setTModalInfo(null)
        addToast(`Acuerdo aceptado exitosamente`, 'success')
        obtenerFabricante(loginStore.fabricante.id)
      }
    })
  }

  const uppy = React.useMemo(() => {
    const fabricante_id = loginStore.fabricante ? loginStore.fabricante.id : ''
    return Uppy({
      restrictions: { allowedFileTypes: ['image/*', 'application/pdf'], maxNumberOfFiles: 1 },
      meta: {
      },
      locale: Espanol,
    })
      .use(Webcam)
      .use(XHRUpload, {
        endpoint: `${rootStore.environment.api.config.url}/fabricante/${fabricante_id}/`,
        headers: {
          Authorization: 'Token ' + loginStore.token,
        },
        method: 'PATCH',
        formData: true,
        fieldName: 'constancia_ruc',
      })
      .use(ImageCompressor, {
        maxHeight: 2000,
        maxWidth: 2000,
        quality: 0.7,
      })
  }, [])

  useEffect(() => {
    uppy.on('complete', (result: any) => {
      if (!loginStore.fabricante) {
        return
      }
      if (result.successful.length) {
        obtenerFabricante(loginStore.fabricante.id)
        addToast("Constancia subida exitosamente", "success")
        setUppyVisible(false)
      } else {
        addToast("Ocurrió un error al subir la constancia, vuelva a intentar en unos momentos", "error")
      }
    })
    return () => uppy.close()
  }, [])

  return (
    <>
      {!!Fabricante && (
        <Page login_required fabricante={true} disableScroll={true} id={'documentos-fabricante'}>
          <DashboardModal
            uppy={uppy}
            closeModalOnClickOutside
            open={UppyVisible}
            onRequestClose={() => {
              setUppyVisible(false)
              uppy.reset()
            }}
            plugins={['Webcam']}
            metaFields={[{ id: 'name', name: 'Nombre', placeholder: 'Nombre del archivo' }]}
          />
          <ModalInfo
            color={'var(--azul-fabricante)'}
            isOpen={TModalInfo !== null}
            close={() => setTModalInfo(null)}
            titulo={TModalInfo === 'contrato' ? 'Acuerdo Comercial de ABi' : 'Constancia de RUC'}
            cssClass={'modal-info-contrato-fabricante'}
          >
            {TModalInfo === 'contrato' && (
              <div className="d-flex flex-column" style={{ justifyContent: 'space-between', flex: 1 }}>
                {!!Fabricante.fecha_aceptacion_contrato && (
                  <span>Acuerdo aceptado el: {moment(Fabricante.fecha_aceptacion_contrato).format('DD/MM/YYYY')}</span>
                )}
                <div className="row m-1">
                  <button className="btn" onClick={() => obtenerContrato()}>
                    Descargar Acuerdo Comercial
                  </button>
                  {!Fabricante.fecha_aceptacion_contrato && (
                    <button className="btn mt-3" onClick={() => aceptarContrato()}>
                      Aceptar Acuerdo Comercial
                    </button>
                  )}
                </div>
              </div>
            )}
            {TModalInfo === 'ruc' && (
              <>
                <p>Tenes que ser contribuyente para trabajar con ABi.</p>
                <p>Acá tenes que subir la constancia de RUC que emite la SET.</p>
              </>
            )}
          </ModalInfo>

          <div className="container-menu-perfil-transportista" style={{ alignItems: 'center' }}>
            <SubHeader noContainer links={[{ titulo: 'Configuración', ruta: RUTAS_APP_FABRICANTE.CONFIGURACION }, { titulo: 'Documentos' }]} />
            <div className="titulo">Documentos</div>
            {Loading && <IonSpinner />}
            {!Loading && (
              <div className={Movil ? 'container-lg' : ''}>
                <p className="text-center text-lg-left">
                  Aquí podés subir los documentos solicitados y descargar los contratos de ABi.
                </p>
                <div id="documentos-transportista-config">
                  <div className='d-flex flex-row align-items-center mb-3'>
                    <div className="alert-icon-container">
                      {!Fabricante.constancia_ruc && (
                        <FIcon.AlertCircle color="var(--naranja-abi)" size={20} className='mr-2' />
                      )}
                    </div>

                    <div className="btn btn-outline-secondary" onClick={() => setUppyVisible(true)}>
                      <FIcon.Camera size={20} className="mr-2" />
                      <span>Constancia de RUC</span>
                    </div>

                    <span className='ml-2 cpointer' style={{width: '30px'}} onClick={() => setTModalInfo("ruc")}>
                      <img src="assets/icon/info.png" width="30px" height="30px"/>
                    </span>
                  </div>
                  
                  <div className='d-flex flex-row align-items-center'>
                    <div className="alert-icon-container">
                      {!Fabricante.fecha_aceptacion_contrato && (
                        <FIcon.AlertCircle color="var(--naranja-abi)" size={20} className='mr-2' />
                      )}
                    </div>

                    <div className="btn btn-outline-secondary" onClick={() => setTModalInfo("contrato")}>
                      <AiOutlineEye size={20} className="mr-2" />
                      <span>Acuerdo Comercial de ABi</span>
                    </div>
                  </div>
                </div>
              </div>
            )}
          </div>
        </Page>
      )}
    </>
  )
})

export default FabricanteDocumentos
